import React, { useState, useEffect } from 'react'
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import NumberFormat from 'react-number-format'
import { useForm } from '../../hooks/useForm';
import { alert_warning } from '../../../helpers/Notificacion';
import { Form } from 'react-bootstrap';
export const FilaTipoPresentacion = ({ listTipoPresentacion,defaultData, handleAddFila, tipoPresentacionId = 0, setDetalleContraparte, detalleContraparte, setAddNuevo, editarItem = false, monto = 0, setEditarItem }) => {
    const [itemListSeleccionado, setItemListSeleccionado] = useState(null);
    const [default_compra, setDefault_compra] = useState(false);
    const [default_venta, setDefault_venta] = useState(false);
    const [values, handleOnChange, , setValues] = useForm({tipoPresentacion: {},...defaultData});

    useEffect(() => {
        setDefault_compra(defaultData.default_compra);
        setDefault_venta(defaultData.default_venta);
    }, []);
    const onchangeListItemSelected = (event, nuevoValor) => {
        setValues({ ...values, tipoPresentacion: nuevoValor });
        setItemListSeleccionado(nuevoValor);
    }
    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
                addNuevaFila();
        }
    }
    const addNuevaFila = () => {
        if (values) {
            let { tipo_presentacionId = 0, nombre } = values.tipoPresentacion;
            if (Number(tipo_presentacionId) > 0) {
                let { unidades = "0",
                    precio_compra = "0",
                    precio_venta = "0",
                    default_compra = false,
                    default_venta = false } = values;
                unidades = String(unidades).replace(",", "");
                if (Number(unidades) > 0) {
                    precio_compra = String(precio_compra).replace("Q ", "");
                    precio_compra = String(precio_compra).replace(",", "");
                    precio_venta = String(precio_venta).replace("Q ", "");
                    precio_venta = String(precio_venta).replace(",", "");

                    if (Number(precio_compra) > 0) {
                        if (Number(precio_venta) > 0) {
                            if (Number(precio_venta) >= Number(precio_compra)) {
                                let itemNuevaFila = {};
                                itemNuevaFila.tipo_presentacionId = tipo_presentacionId;
                                itemNuevaFila.nombre = nombre;
                                itemNuevaFila.unidades = unidades;
                                itemNuevaFila.precio_compra = precio_compra;
                                itemNuevaFila.precio_venta = precio_venta;
                                itemNuevaFila.default_compra = default_compra;
                                itemNuevaFila.default_venta = default_venta;
                                itemNuevaFila.editar = false;
                                handleAddFila(itemNuevaFila);
                                setAddNuevo(false);
                            } else {
                                alert_warning("El precio de venta debe ser mayor o igual al precio de compra");
                            }
                        } else {
                            alert_warning("El precio de venta debe ser mayor a 0");
                        }
                    } else {
                        alert_warning("El precio de compra debe ser mayor a 0");
                    }
                } else {
                    alert_warning("El input de unidades debe ser mayor a 0");
                }
            } else {
                alert_warning("Por favor complete la información solicitada");
            }
        } else {
            alert_warning("Por favor complete la información solicitada");
        }
    }

    useEffect(() => {
        if (Number(tipoPresentacionId) > 0) {
            let item = listTipoPresentacion.find(i => Number(i.id) === Number(tipoPresentacionId));
            setItemListSeleccionado(item);
            setValues({ ...values, contraparte: item });
        }
    }, [listTipoPresentacion]);
    
    useEffect(() => {
        if (Number(tipoPresentacionId) > 0) {
            let item = listTipoPresentacion.find(i => Number(i.tipo_presentacionId) === Number(tipoPresentacionId));
            setItemListSeleccionado(item);
            setValues({ ...values, tipoPresentacion: item });
        }
    }, [listTipoPresentacion]);

    const handleEditItemContraparte = () => {
        if (values) {
            let { id = 0, nombre } = values.contraparte;
            if (Number(id) > 0) {
                let { monto = "0" } = values;

                monto = String(monto).replace("Q ", "");
                monto = String(monto).replace(",", "");
                if (Number(monto) > 0) {
                    let itemDetalle = detalleContraparte.map(item => {
                        if (Number(item.tipoPresentacionId) === Number(tipoPresentacionId)) {
                            item.monto = monto;
                            item.nombre = nombre;
                            item.tipoPresentacionId = id;
                            item.editar = false;
                            return item;
                        } else {
                            return item;
                        }
                    });
                    setDetalleContraparte(itemDetalle);
                    setAddNuevo(false);
                    setEditarItem(false);
                } else {
                    alert_warning("El monto debe ser mayor a 0");
                }
            } else {
                alert_warning("Por favor complete la información solicitada");
            }
        } else {
            alert_warning("Por favor complete la información solicitada");
        }
    }
    const handleDefaultCompra = () => {
        setDefault_compra(!default_compra);
        setValues({
            ...values,
            default_compra: !default_compra
        });
    }
    const handleDefaultVenta = () => {
        setDefault_venta(!default_venta);
        setValues({
            ...values,
            default_venta: !default_venta
        });
    }

    return (
        <tr>
            <td></td>
            <td>
                <div className="mb-3 input-group">
                    {
                        <>
                            <Autocomplete
                                value={itemListSeleccionado || null}
                                id="items_contraparte"
                                name="items_contraparte"
                                options={listTipoPresentacion || []}
                                getOptionLabel={(option) => option.nombre}
                                style={{ width: 300 }}
                                size="small"
                                onChange={onchangeListItemSelected}
                                renderInput={(params) => <TextField {...params} label="Contraparte" />}
                            />
                        </>
                    }

                </div>
            </td>
            <td>
                <NumberFormat
                    id="unidades"
                    name="unidades"
                    className="form-control"
                    thousandSeparator prefix=""
                    value={values.unidades}
                    onChange={handleOnChange}
                    onKeyPress={handleKeyPress} />
            </td>
            <td>
                <NumberFormat
                    id="precio_compra"
                    name="precio_compra"
                    className="form-control"
                    thousandSeparator prefix="Q "
                    value={values.precio_compra}
                    onChange={handleOnChange}
                    onKeyPress={handleKeyPress} />
            </td>
            <td>
                <NumberFormat
                    id="precio_venta"
                    name="precio_venta"
                    className="form-control"
                    thousandSeparator prefix="Q "
                    value={values.precio_venta}
                    onChange={handleOnChange}
                    onKeyPress={handleKeyPress} />
            </td>
            <td>
                <Form.Control type="checkbox" id={`compra_tipo_presentacionId${values.tipo_presentacionId}`} checked={default_compra} onChange={handleDefaultCompra} />
                <Form.Label htmlFor={`compra_tipo_presentacionId${values.tipo_presentacionId}`} className="cr" />
            </td>
            <td>
                <Form.Control type="checkbox" id={`venta_tipo_presentacionId${values.tipo_presentacionId}`} checked={default_venta} onChange={handleDefaultVenta} />
                <Form.Label htmlFor={`venta_tipo_presentacionId${values.tipo_presentacionId}`} className="cr" />
            </td>
            <td>
                {
                    editarItem === false ?
                        <button
                            type="button"
                            className="btn-icon btn btn-success btn-sm"
                            onClick={() => { addNuevaFila() }}>
                            <i className="feather icon-plus" />
                        </button>
                        : <button
                            type="button"
                            className="btn-icon btn btn-primary btn-sm"
                            onClick={() => { addNuevaFila() }}>
                            <i className="feather icon-edit" />
                        </button>
                }

            </td>
        </tr>
    )
}
